"use client";

import { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import Link from "next/link";
import { fetchUserPosts, fetchUsers } from "../../services/api";
import { useGlobalState } from "../../services/globalstate";

export default function UserPostsPage() {
  const { id } = useParams(); // Get user ID from URL
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  const {users,fetchingUsers} = useGlobalState();
  console.log(users);

  useEffect(() => {
    const loadPosts = async () => {
      try {
        const data = await fetchUserPosts(id);
        setPosts(data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    loadPosts();
   
  }, [id]);

  useEffect(()=>{
    fetchingUsers();

  },[])

  return (
    <div className="container mx-auto p-4">
      <Link href="/users" className="text-blue-500 underline">
        ‚Üê Back to Users
      </Link>
    <h1 className="text-2xl font-bold my-4">Posts by User {users.name}</h1>

      {loading ? (
        <p>Loading posts...</p>
      ) : error ? (
        <p className="text-red-500">{error}</p>
      ) : (
        <ul className="mt-2">
          {posts.map((post) => (
            <li key={post.id} className="border p-2 rounded-lg mb-2">
              <h3 className="font-semibold">{post.title}</h3>
              <p>{post.body}</p>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
